body {
  font-family: Helvetica, sans-serif;
}

.react-autosuggest__container {
  position: relative;
}

.react-autosuggest__input {
  width: 240px;
  height: 30px;
  padding: 10px 20px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  _top: 0;
  width: 100%;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}

#async-example .dropdown-item {
  display: block;
  width: 100%;
  padding: 0.25rem 1.5rem;
  clear: both;
  font-weight: 400;
  color: #212529;
  text-align: inherit;
  white-space: nowrap;
  background-color: transparent;
  border: 0;
}

.react-datepicker__input-container input.form-control {
  margin-bottom: -5px !important;
}


/* form-contract-style */
#box-contract label{
  margin-top: 10px;
}
#form-contract h4, #form-contract h5{
  font-weight: bold;
}
#form-contract h4{
  margin-top: 25px;
}
#form-contract h5{
  margin: 15px 0 0;
}
#form-contract input[name="customerName"], #form-contract input[name="partnerName"]{
  text-transform: uppercase;
}
#form-contract .input-group .form-control{
  z-index: 1!important;
}
#form-contract .react-datepicker-popper{
  z-index: 3;
}

#form-contract>.row{
  padding-left: 10px;
}

#form-contract .react-datepicker__day--outside-month{
  color: #ccc;
}
